import React from 'react'


export const Footer = () => {
    let footerStyle={
        position: "absolute",
        width:"100%",
        border: "2px solid red"
    }
    return (
        <footer className="bg-dark text-light text-center my-3 py-3" style={footerStyle}>
            <p>Copyright &copy; MyTodosList.com</p>
        </footer>
    )
}
